*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}
html,
body {
  font-size: 16px;
  font-family: 'Source Sans Pro', 'sans-serif';
  -webkit-font-smoothing: antialiased;
  color: rgba(17,17,17,0.8);
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  height: 100%;
  min-height: 100%;
  background: #fff;
}
::selection {
  background: #1c51ff;
  color: #fff;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 0.4em;
}
h1,
.alpha {
  font-size: 3em;
}
h2,
.beta {
  font-size: 1.8em;
}
h3,
.gamma {
  font-size: 1.5em;
}
h4,
.delta {
  font-size: 1.2em;
}
h5,
.epsilon {
  font-size: 1.1em;
}
h6,
.zeta {
  font-size: 1.05em;
}
p {
  margin-bottom: 24px;
  line-height: 1.5em;
}
a {
  color: #333;
  font-weight: 700;
  text-decoration: none;
}
a:hover {
  color: #474747;
}
hr {
  border: none;
  border-bottom: 1px solid $grey;
  width: 100%;
}
.byline {
  display: flex;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -o-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -o-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  height: 150px;
}
.byline * {
  margin: 0;
  padding: 0;
  margin-right: 15px;
  line-height: 1;
}
.byline:before,
.byline:after {
  content: "";
  display: block;
  height: 0;
  box-flex: 1;
  border-bottom: 1px dotted currentcolor;
  padding-top: 1px;
  -webkit-box-flex: 1;
  -moz-box-flex: 1;
  -o-box-flex: 1;
  -ms-box-flex: 1;
  box-flex: 1;
  -webkit-flex-grow: 1;
  flex-grow: 1;
}
.byline:before {
  margin-right: 15px;
}
.button,
button,
.button-list li {
  display: inline-block;
  padding: 12px 15px;
  background: #1c51ff;
  color: rgba(255,255,255,0.9);
  border: none;
  text-align: center;
  font-weight: normal;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: all 300ms ease-in-out;
  -moz-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  -ms-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}
.button-overlay,
button-overlay,
.button-list li-overlay {
  background: #2ecc40;
  position: absolute;
  top: 0;
  left: 0;
  padding: 12px 15px;
  width: 100%;
  height: 100%;
  -webkit-animation: buttonOverlay 150ms ease-in-out;
  -moz-animation: buttonOverlay 150ms ease-in-out;
  -o-animation: buttonOverlay 150ms ease-in-out;
  -ms-animation: buttonOverlay 150ms ease-in-out;
  animation: buttonOverlay 150ms ease-in-out;
}
.button:hover,
button:hover,
.button-list li:hover {
  color: #fff;
  background: #003bff;
}
.button:focus,
button:focus,
.button-list li:focus {
  outline: none;
}
.button.alt,
button.alt,
.button-list li.alt {
  background: #333;
}
.button.alt:hover,
button.alt:hover,
.button-list li.alt:hover {
  background: #2e2e2e;
}
.button-bar {
  display: flex;
}
.button-bar > * {
  -webkit-box-flex: 1;
  -moz-box-flex: 1;
  -o-box-flex: 1;
  box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
@-moz-keyframes buttonOverlay {
  0% {
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    filter: alpha(opacity=0);
    -webkit-transform: scale(1.4);
    -moz-transform: scale(1.4);
    -o-transform: scale(1.4);
    -ms-transform: scale(1.4);
    transform: scale(1.4);
  }
  100% {
    opacity: 1;
    -ms-filter: none;
    filter: none;
    -webkit-transform: none;
    -moz-transform: none;
    -o-transform: none;
    -ms-transform: none;
    transform: none;
  }
}
@-webkit-keyframes buttonOverlay {
  0% {
